// SECTION scheduler :: START

.scheduler {
  &__title {
    padding-bottom: 40px;
    font-weight: 900;
    font-size: 40px;
    line-height: 50px;
    color: #000;
  }

  &__header {
    overflow: hidden;
    position: relative;
    background-color: #ffffff;

    .c-grid {
      position: relative;
    }

    &-wrapper {
      display: flex;
      align-items: center;
      height: 158px;

      &-left {
        display: flex;
        align-items: center;

        span {
          display: block;
          width: 2px;
          height: 38px;
          margin-left: 20px;
          margin-right: 20px;
          background-color: rgba(#00449D, 0.1);
        }

        p {
          font-weight: 300;
          font-size: 20px;
          color: #000;
        }
      }
    }

    &-marker {
      position: absolute;
      top: -6px;
      right: 0;
    }

    svg {
      fill: #00449D;
    }
  }

  &__main {
    padding-top: 110px;
    padding-bottom: 120px;
    background-color: #f8fafc;

    &-subtitle {
      padding-bottom: 24px;
      font-weight: 300;
      font-size: 26px;
      color: #000;
    }

    &-title {
      padding-bottom: 37px;
      font-weight: normal;
      font-size: 50px;
      line-height: 70px;
      color: #000;
    }

    &-heading {
      padding-bottom: 40px;
      font-weight: 900;
      font-size: 60px;
      line-height: 70px;
      color: #000;
    }

    &-dropdown {
      position: relative;
      display: flex;
      align-items: center;

      &-wrapper {
        display: flex;
        align-items: center;
        padding-bottom: 50px;

        > div {
          padding-right: 60px;
        }
      }

      &-title {
        padding-right: 16px;
        font-size: 20px;
        color: #222;
      }

      &-btn {
        cursor: pointer;
        padding-right: 7px;
        font-size: 20px;
        color: #00449D;

        svg {
          width: 10px;
          height: 10px;
          transform: rotate(90deg);
          fill: #00449D;
        }
      }

      &-menu {
        z-index: 899;
        display: none;
        position: absolute;
        top: calc(100% + 5px);
        right: 0;
        padding-top: 2px;
        padding-bottom: 3px;
        border-radius: 10px;
        background-color: #fff;
        box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);
      }

      &-link {
        display: flex;
        justify-content: flex-end;
        padding: 5px 15px 5px 30px;
        color: #00449D;
        font-size: 16px;
        line-height: 35px;
        @include transition(background-color);

        &:hover {
          background-color: lighten(#00449D, 67%);
          @include transition(background-color);
        }
      }
    }

    &-box {
      cursor: pointer;
      height: 100%;
      padding: 30px 15px 30px 30px;
      border-radius: 10px;
      background: #fff;
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);
      @include transition(background-color);

      &:hover {
        background-color: #00449d;
        @include transition(background-color);

        p {
          color: #ffffff;
          @include transition(color);
        }
      }

      &-wrapper {
        display: flex;
        flex-wrap: wrap;
        width: calc(100% + 40px);
        margin-left: -40px;

        > div {
          width: 100%;
          max-width: calc(100% / 3);
          padding-left: 40px;
          padding-bottom: 40px;
        }
      }

      p {
        @include transition(color);
      }

      &-title {
        padding-bottom: 5px;
        font-weight: 900;
        font-size: 22px;
        color: #000;
      }

      &-subtitle {
        padding-bottom: 15px;
        font-size: 18px;
        color: #222;
      }

      &-desc {
        overflow: hidden;
        position: relative;
        height: 44px;
        font-size: 16px;
        line-height: 22px;
        color: rgba(#222, 0.4);

        //textarea {
        //  z-index: -1;
        //  opacity: 0;
        //  visibility: hidden;
        //  resize: none;
        //  position: absolute;
        //  top: 0;
        //  left: 0;
        //  width: 100%;
        //  height: auto;
        //  padding: 0;
        //  margin: 0;
        //  border: none;
        //  border-radius: 0;
        //  box-shadow: none;
        //  background-color: transparent;
        //  line-height: 22px;
        //}
      }
    }

    &-btn {
      display: none;
      
      .c-btn {
        max-width: unset;
        width: auto;
        padding-left: 60px;
        padding-right: 60px;

        &.is-active {
          svg {
            transform: rotate(-90deg);
            @include transition(transform);
          }
        }

        p {
          padding-right: 5px;
        }

        span {
          padding-right: 20px;
        }
      }

      svg {
        position: relative;
        top: 2px;
        width: 12px;
        height: 12px;
        transform: rotate(90deg);
        backface-visibility: hidden;
        transform-origin: center;
        @include transition(transform);
      }
    }
  }

  &__data {
    padding-top: 129px;
    padding-bottom: 120px;

    &-list {
      display: flex;
      flex-direction: column;
      padding-bottom: 115px;

      li {
        display: flex;
        align-items: center;
        padding-bottom: 20px;

        &:last-of-type {
          padding-bottom: 0;
        }

        > div {
          &:nth-of-type(1) {
            width: 40px;
            padding-top: 3px;
          }
          &:nth-of-type(2) {
            width: 100%;
            max-width: 180px;
          }
          &:nth-of-type(3) {
            flex: 1;
            min-width: 1px;
            max-width: 100%;
          }
        }

        p,
        span {
          font-size: 20px;
          color: #000;
        }

        p {
          font-weight: 700;
        }
      }
    }
  }

  &__time {}

  &__information {}
}

// SECTION scheduler :: END